import axios from "axios";
import qs from "querystring";

let instance = axios.create({
    timeout: 5000,
});

instance.interceptors.request.use(
    function (config) {
        if (config.method === "post") {
            config.data = qs.stringify(config.data);
        }
        return config;
    },
    function (error) {
        return Promise.reject(error);
    }
);

instance.interceptors.response.use(
    function (response) {
        return response;
    },
    function (error) {
        ErrorHandle(error.response.status, error.message);
        return Promise.reject(error);
    }
);

function ErrorHandle(status, message) {
    switch (status) {
        case 400:
            console.log("400");
            break;
        case 401:
            console.log("401");
            break;
        case 403:
            console.log("403");
            break;
        case 404:
            console.log("404");
            break;
        case 500:
            console.log("500");
            break;
        case 503:
            console.log("503");
            break;
        default:
            console.log(message);
            break;
    }
}

export default instance;