<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  mounted() {
    this.pageSkip()
  },
  updated() {
    this.pageSkip()
  },
  methods: {
    pageSkip() {
      if (/Android|webOS|iPhone|iPod|BlackBerry|iPad|SymbianOS/i.test(navigator.userAgent)) {
        if (this.$route.path.startsWith("/mobile")) {
        } else {
          let path = this.$route.path
          path.endsWith("/") ? this.$router.replace(`${path}mobile`) : this.$router.replace(`/mobile${path}`)
        }
      } else {
        let path = this.$route.path
        console.log(path)
        if (this.$route.path.startsWith("/mobile")) {
          let path = this.$route.path
          let newPath = path.replace('/mobile', '');
          if (newPath === "") {
            this.$router.replace("/")
          } else {
            this.$router.replace(newPath)
          }
        }
      }
    }
  }
}
</script>

